h1,
h2,
h3,
h4,
h5,
h6,
p {
    margin: 0px;
}

ul,
li {
    margin: 0px;
    padding: 0px;
}
img {
    display: block;
    max-width: 100%;
    height: auto;
}

body {
    font-family: var(--main-font);
    color: var(--font-color);
}

